@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');


.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #000000;
  color: white;
  font-family: 'Press Start 2P', cursive;
}

h1 {
  margin-bottom: 20px;
}

.Game {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Grid {
  margin-bottom: 20px;
}

.Controls {
  display: grid;
  grid-template-columns: repeat(3, 50px);
  gap: 10px;
}

.Controls button {
  width: 50px;
  height: 50px;
  font-size: 18px;
  cursor: pointer;
}

.Controls .up {
  grid-column: 2;
}

.Controls .left {
  grid-column: 1;
  grid-row: 2;
}

.Controls .down {
  grid-column: 2;
  grid-row: 2;
}

.Controls .right {
  grid-column: 3;
  grid-row: 2;
}
