@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

body {
    font-family: 'Press Start 2P', cursive;
    background-color: #000000;
    color: #FFFFFF;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

.Game {
    display: flex;
    justify-content: space-between;
    width: 80%;
    padding: 20px;
    background-color: #000000;
    border: 3px solid #FF00FF;
    border-radius: 10px;
}

.scorePanel, .gamePanel, .nextPanel {
    background-color: #000000;
    border: 3px solid #FF00FF;
    border-radius: 10px;
    padding: 10px;
    margin: 5px;
    color: #FFFFFF;
    text-align: center;
    width: 150px; /* Sesuaikan lebar panel */
}

.gamePanel {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Controls button {
    background-color: #000000;
    color: #FF00FF;
    border: 3px solid #FF00FF;
    border-radius: 5px;
    margin: 2px;
    cursor: pointer;
    font-family: 'Press Start 2P', cursive;
}

.Controls button:hover {
    background-color: #FF00FF;
    color: #FFFFFF;
}

.Controls .rotate {
    margin-bottom: 5px;
}

.GameOverModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
}

.GameOverContent {
    background-color: #000000;
    padding: 20px;
    border: 3px solid #FF00FF;
    border-radius: 10px;
    text-align: center;
    color: #FFFFFF;
}

.GameOverContent button {
    background-color: #000000;
    color: #FF00FF;
    border: 3px solid #FF00FF;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    font-family: 'Press Start 2P', cursive;
}

.GameOverContent button:hover {
    background-color: #FF00FF;
    color: #FFFFFF;
}

.WelcomeModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
}

.WelcomeContent {
    background-color: #000000;
    padding: 20px;
    border: 3px solid #FF00FF;
    border-radius: 10px;
    text-align: center;
    color: #FFFFFF;
    box-shadow: 0 0 20px rgba(255, 0, 255, 0.5);
}

.WelcomeContent h1 {
    font-size: 30px;
    margin-bottom: 10px;
}

.WelcomeContent p {
    margin-bottom: 20px;
}

.WelcomeContent button {
    background-color: #000000;
    color: #FF00FF;
    border: 3px solid #FF00FF;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    font-family: 'Press Start 2P', cursive;
    transition: all 0.2s;
}

.WelcomeContent button:hover {
    background-color: #FF00FF;
    color: #FFFFFF;
}

.Controls input[type="range"] {
    -webkit-appearance: none;
    appearance: none;
    width: 100%;
    height: 5px;
    background: #FF00FF;
    border-radius: 5px;
    outline: none;
}

.Controls input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 15px;
    height: 15px;
    background: #FFFFFF;
    border: 2px solid #FF00FF;
    border-radius: 50%;
    cursor: pointer;
}

.Controls input[type="range"]::-moz-range-thumb {
    width: 15px;
    height: 15px;
    background: #FFFFFF;
    border: 2px solid #FF00FF;
    border-radius: 50%;
    cursor: pointer;
}
